.arrow-down-target {
  width: 0;
  height: 0;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
  border-top: 12px solid #595959;
  position: relative;
  top: 0vh;
  right: 8vw;
}

.title-intro-tar{
  font-family: 'Karantina', cursive;
  font-size: 16vw;
  margin-top: 32vh;
  width: 100%;
  text-align: center;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  color: rgb(86, 195, 232);
}

.sub-intro-tar{
  font-family: 'Heebo', sans-serif;
  font-size: 5vw;
  color: grey;
  text-align: center;
  margin-top: 53vh;
  width: 82vw;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  direction: rtl;
}

.photo-target-intro{
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  width: 20vw;
  margin-top: 18vh;
}

.title-Target {
  font-family: 'Karantina', cursive;
  font-size: 13vw;
  margin-bottom: 3vw;
  margin-top: 32vh;
  width: 48%;
  text-align: center;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  transition: color 0.3s;
}


.all-option-step1 {
  font-family: 'Heebo', sans-serif;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  text-align: center;
  color: #595959;
  margin-top: 8vh;
  font-weight: normal;
}

.array-item-step1 {
  height: 6vh;
  width: 85vw;
  background-color: #d9d9d9;
  margin-top: 2vh;
  padding-top: 1.5vh;
  font-size: 5vmin;
  cursor: pointer;
  /* Add pointer cursor on hover */
  margin-bottom: 3vh;
}

.array-item:active {
  background-color: #56c3e8;
  /* Change background color to blue when selected */
  color: white;
  /* Change text color to white for better readability */
}

#text-Target1 {
  font-family: 'Heebo', sans-serif;
  font-size: 5vmin;
  color: grey;
  text-align: center;
  margin-top: 2vh;
  width: 90%;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  direction: rtl;
}

#text-Target2 {
  font-family: 'Heebo', sans-serif;
  font-size: 5vmin;
  color: grey;
  text-align: center;
  margin-top: 8vh;
  width: 90%;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  direction: rtl;
}

.all-option-step2 {
  font-family: 'Heebo', sans-serif;
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  text-align: center;
  margin-top: 16vh;
  color: white;
}

.array-item-step2 {
  height: 6vh;
  width: 85vw;
  margin-top: 2vh;
  padding-top: 1.5vh;
  font-size: 5.5vmin;
  cursor: pointer;
  /* Add pointer cursor on hover */
  margin-bottom: 3vh;
}

#step2Item0 {
  background-color: #6fa8dc;
}

#step2Item1 {
  background-color: #67d7b2;
}

#step2Item2 {
  background-color: #ea9999;
  margin-bottom: 9vh;
}

.line-array {
  position: absolute;
  right: 50%;
  transform: translateX(50%);
